import React from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom'
import 'assets/styles/base.css'

import CV from 'containers/cv'

function App() {
  return (
    <Router>
      <Route
        render={({ location }) => (
          <div className="cv-app">
            <section className="cv-container">
              <Switch>
                <Route
                  exact
                  path="/"
                  component={() => <CV />}
                />
              </Switch>
            </section>
          </div>
        )}
      />
    </Router>
  )
}

export default App
